.square {
  width: 150px;
  height: 150px;
  background: white;
  border: 1px solid black;
  font-size: 70px;
  cursor: pointer;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.square:hover {
  background: #a3a3a3;
  transition: 0.3s all ease;
}
