.wrapper {
  min-height: 100vh;
  width: 100%;
  background: #222222;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.star_btn {
  border: 1px solid white;
  width: 450px;
  height: 50px;
  font-size: 25px;
  margin-bottom: 50px;
  cursor: pointer;
}
.star_btn:hover {
  background: #a3a3a3;
  transition: 0.2s all ease;
}

.game-info {
  color: white;
  font-size: 25px;
  margin-top: 20px;
}
